.SearchBase{
    position: fixed;
    right: 50px;
    padding-top: 10px;
}
.SearchBase *{
    color: black;
}
.SearchBase form{
    position: relative;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    transition: all 1s;
    width: 50px;
    height: 50px;
    background: white;
    box-sizing: border-box;
    border-radius: 25px;
    border: 4px solid white;
    padding: 5px;
}

.SearchBase input{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;;
    height: 42.5px;
    line-height: 30px;
    outline: 0;
    border: 0;
    display: none;
    font-size: 1em;
    border-radius: 20px;
    padding: 0 20px;
}

.SearchBase .fa{
    box-sizing: border-box;
    padding: 10px;
    width: 42.5px;
    height: 42.5px;
    position: absolute;
    top: 0;
    right: 0;
    border-radius: 50%;
    color: #07051a;
    text-align: center;
    font-size: 1.2em;
    transition: all 1s;
}

.SearchBase form:hover,
.SearchBase form:valid{
    width: 400px;
    cursor: pointer;
}

.SearchBase form:hover input,
.SearchBase form:valid input{
    display: block;
}

.SearchBase form:hover .fa,
.SearchBase form:valid .fa{
    background: #07051a;
    color: white;
}


.SearchBase a {
  display: none;
  position: absolute;
  top: 70px;
  bottom:0;
  left: 0;
  right: 0;
  font-size: 20px;
  color: white;
  text-align: center; 
  width: 100%;
}

.SearchBase form:valid a {
  display: block;
}