.content {
    z-index: -10;
    background: rgb(2, 0, 36);
    background: radial-gradient(circle, rgba(2, 0, 36, 0) 78%, rgba(0, 0, 0, 0.8587767870820203) 100%);
    display: flex;
    position: absolute;
    margin: 0px;
    padding-top: 80px;
    top: 0px;
    left: 0px;
    overflow: scroll;
    /* justify-content: space-evenly; */
    min-width: 100vw;
    max-width: 100vw;
    min-height: 100vh;
    max-height: 100vh;
    scrollbar-width: none;
    gap: 20px;

    div {
        margin: 10px;
    }
}

.content div {
    opacity: 0.6;
}


.content>div {
    margin-inline: auto;
}



.content>*:not(.background) {
    padding-top: 20px;
    opacity: 0;
}

.allBody .background {
    transition: opacity 4s ease-out;
    z-index: -10;
    position: fixed;
    top: 0px;
    width: 100vw;
    background: rgb(2, 0, 36);
    background: linear-gradient(90deg, rgba(2, 0, 36, 1) 81%, rgba(5, 25, 31, 1) 100%);
    /* Full height */
    height: 100vh;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}